import React from "react";
import { Link } from "react-router-dom";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { postData } from "../../../../config/axios.jsx";
import HeadFormBatalService from "./HeadFormBatalService.jsx";
import * as moment from "moment-timezone";

// import HeadFormBatalBeli from "./HeadFormBatalBeli.jsx";
// import HeadFormBatalService from "./HeadFormBatalService.jsx";

class FormLaporanBatalService extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      dataservices: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    postData("service/get/servicebatal", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            export: false,
            isLoading: false,
            dataservices: [],
          });
        } else {
          ToastNotification("success", "Laporan Batal Service Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            dataservices: res.data,
          });
        }
      })
      .catch((err) => {
        this.setState({
          export: false,
          isLoading: false,
        });
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Batal Services</li>
        </ol>
        <h1 className="page-header">Laporan Batal Services </h1>
        <Panel>
          <PanelHeader>Laporan Batal Services</PanelHeader>
          <br />
          {/* Laporan Batal Services */}
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFormBatalService
                  export={this.state.export}
                  isLoading={this.state.isLoading}
                  dataservices={this.state.dataservices}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
          <br />
          {/* End Tambah Laporan Batal Services  */}
        </Panel>
      </div>
    );
  }
}

export default FormLaporanBatalService;
