import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  getNokirim, getTimbangBarang,
} from "../../../actions/databarang_action.jsx";

import { connect } from "react-redux";
import {  getToday, localStoragedecryp,} from "../../../components/helpers/function.jsx";
import FormTimbangBarang from "./FormTimbangBarang.jsx";
const maptostate = (state) => {
  return {
    timbang_barang : state.databarang.getTimbangBarang
  };
};

class TimbangBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      isLoadingBtn: false,
      DataTimbangBarang: [],
      SimpanDataBarang: false,
      resetTimbangBarang : false,
      
      products : [
        {
          gudang : "TOKO",
          baki : "KLNG",
          qty : 10,
          berat : 200,
          berat_baki : 100,
          berat_bandrol: 0.200,
          total: 1250,
          timbang: 0,
          selisih : 0
        },
        {
          gudang : "TOKO",
          baki : "GLNG",
          qty : 10,
          berat : 200,
          berat_baki : 100,
          berat_bandrol: 0.200,
          total: 1250,
          timbang: 0,
          selisih : 0
        }
      ]
    };

  }

  componentDidMount() {
    this.props.dispatch(getNokirim());
    this.setState({
      products : localStoragedecryp("timbang_barang")
    })
  }

  handleSubmit(data){
      this.props.dispatch(getTimbangBarang(data.kode_gudang, data.kode_baki, getToday()))
      
  }


  render() {
    console.log(this.props.products);
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Timbang Baki</li>
        </ol>
        <h1 className="page-header">Timbang Baki </h1>
        <Panel>
          <PanelHeader>Timbang Baki</PanelHeader>
          <br />

          {/* Terima Barang */}
          <div className="container">
            <FormTimbangBarang
            handleChange={() => this.handleChange()}
              column={this.state.columns || []}
              products={this.props.timbang_barang|| []}
            onSubmit={(data) => this.handleSubmit(data) }
            />
          
          </div>
          <br />
          {/* End Tambah Terima Barang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(TimbangBarang);
