const ValidasiParamterPoint = (value, event) => {
    const errors = {};
    // console.log(event.charCode==13);
    if (!value.kode_group) {
      errors.kode_group = "Kode Group Tidak Boleh Kosong";
    }
    if (!value.berat) {
      errors.berat = "Berat Tidak Boleh Kosong";
    }
    return errors;
  };
  
  export default ValidasiParamterPoint;
  