import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import LaporanBatalServicePdf from "./pdf/LaporanBatalServicePdf";
import Loading from 'react-fullscreen-loading';

class HeadFormBatalService extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }
  exportPdf() {
    LaporanBatalServicePdf(this.props.dataservices)
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let databarang = [];
    this.props.dataservices.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        databarang.push(item)
        // item.detail_barang.forEach((detail_barang, index) => {
        //   total_ongkos = total_ongkos +  parseInt(detail_barang.ongkos) * parseInt(detail_barang.jumlah);
        //   total_jumlah = total_jumlah + detail_barang.jumlah;
        //   total_berat = total_berat + detail_barang.berat;
        // })
        // total_barang = total_barang + item.detail_barang.length;
      });
    });

    const columns = [
      {
        dataField: "no_faktur_service",
        text: "No Faktur",
        footerAttrs: {
          colSpan: "7",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Services : {databarang.length} </div>;
        },
      },
      {
        dataField: "tgl_system",
        text: "Tanggal Transaksi",
      },
      // {
      //   dataField: "tgl_selesai",
      //   text: "Tanggal Selesai",
      // },
      {
        dataField: "kode_sales",
        text: "Kode Sales",
      },
      {
        dataField: "nama_customer",
        text: "Nama Customer",
      },
      {
        dataField: "no_hp",
        text: "No Hp",
      },
      {
        dataField: "alamat_customer",
        text: "Alamat Customer",
      },
      {
        dataField: "kode_gudang",
        text: "Kode Gudang",
      },
    ]
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
        <div className="col-lg-4">
            <Field
              id="tgl_awal"
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_hp"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Batal Service Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            <label htmlFor="">&nbsp;</label>
            <div className="row">
              <div className="col-lg-6">
                <button onClick={()=>this.exportPdf()} className="btn btn-warning btn-block"> Export PDF </button>
              </div>
              <div className="col-lg-6">
                <LaporanBatalServiceExel data={this.props.dataservices} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormBatalService = reduxForm({
  form: "HeadFormBatalService",
  enableReinitialize: true,
})(HeadFormBatalService);
// export default connect()(HeadFormBatalService);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormBatalService);
