// Modal
export const hideModal = () => {
    return (dispatch) => {
        dispatch({
            type: HIDE_MODAL,
            payload: {
                data: false
            }
        })   
    }
}
export const showModal = () => {
    return (dispatch) => {
        dispatch({
            type: SHOW_MODAL,
            payload: {
                data: true
            }
        })   
    }
}
