import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getKondisiPinjaman,GetDataGroup } from "../../../../actions/datamaster_action";
import { ReanderField } from "../../../../components/helpers/field";
import { NumberOnly,Gramasi } from "../../../../Validasi/normalize";
import Loading from 'react-fullscreen-loading';
import ValidasiHutang from "../../../../Validasi/ValidasiHutang";

class ModalTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ModalWebCam: false,
      isLoading: false,
      harga_jual: true,
      berat_jual: true,
      hargapergram: true,
      ongkos: true,
      total: true,
      gambar: false,
    };
  }
  componentDidMount() {
    this.props.dispatch(getKondisiPinjaman());
    this.props.dispatch(GetDataGroup());
    setTimeout(() => {
        document.getElementById('nama_barang_hutang').focus()
    }, 100);
  }
 
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              id="nama_barang_hutang"
              name="nama_barang"
              component={ReanderField}
              type="text"
              label="Nama Barang"
              placeholder="Masukan Nama Barang"
            />
          </div>
          {/* <div className="col-4">
          <Field
              label="Kondisi Barang"
              name="kondisi_barang"
              options={this.props.datakondisi
                .map((list) => {
                  let data = {
                    value: list.kondisi_barang,
                    name: list.kondisi_barang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kondisi Barang"}
              component={ReanderSelect}
            />
          </div> */}
          {/* <div className="col-4">
          <Field
              label="Kategori"
              name="kategori"
              options={this.props.kategori.filter(
                (list) => list.kode_group !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group +' - '+ list.nama_group,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kategori"}
              component={ReanderSelect}
            />
          </div> */}
          <div className="col-4">
          <Field
              name="berat"
              component={ReanderField}
              normalize={Gramasi}
              type="text"
              label="Berat"
              placeholder="Masukan Berat"
            />
          </div>
          <div className="col-4">
          <Field
              name="jumlah"
              defaultValue="1"
              component={ReanderField}
              type="jumlah"
              normalize={NumberOnly}
              label="Jumlah"
              placeholder="Masukan Jumlah"
            />
          </div>
          <div className="col-12 text-right">
            <label>&nbsp;</label>
          <button
              className="btn btn-primary "
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                <>
                  <i className="fas fa-paper-plane"></i> &nbsp; Simpan Data
                </>
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

// ModalTambahBarang = reduxForm({
//   form: "ModalTambahBarang",
//   enableReinitialize: true,
//   validate: ValidasiMasterDataHadiah,
// })(ModalTambahBarang);
// export default connect(maptostate, null)(ModalTambahBarang);

ModalTambahBarang = reduxForm({
  form: "ModalTambahBarang",
  enableReinitialize: true,
  validate: ValidasiHutang,
})(ModalTambahBarang);

export default connect((state) => {
  return {
    initialValues : {
      jumlah : '1'
    },
    datakondisi: state.datamaster.getKondisiPinjaman,
    kategori: state.datamaster.GetDataGroup,
  };
})(ModalTambahBarang);
