import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
// import { getNokirim } from "../../../actions/databarang_action";
import { getDataBaki, getDataBakiByGudang, getDataGudang } from "../../../actions/datamaster_action";
import { ReanderSelect } from "../../../components/helpers/field";
import Loading from 'react-fullscreen-loading';
import { getToday, localStoragedecryp, localStorageencryp } from "../../../components/helpers/function";
import BootstrapTable from "react-bootstrap-table-next";
import cellEditFactory from 'react-bootstrap-table2-editor';
import { updateTimbangBarang } from "../../../actions/databarang_action";
import { postData2 } from "../../../config/axios";
import { ToastNotification } from "../../../components/helpers/notification";

const maptostate = (state) => {
  let data_terima_barang =
  localStorage.getItem("data_terima_barang") === null || undefined
    ? []
    : JSON.parse(localStorage.getItem("data_terima_barang") || []);
    
  return {
    initialValues: {
      no_kirim: data_terima_barang.no_kirim || "",
    },
    datagudang: state.datamaster.getDataGudang,
    databaki: state.datamaster.getDataBaki,
    timbang_barang : state.databarang.getTimbangBarang
  };
};
class FormTimbangBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
        no_kirim: [],
        datagudang:[],
        isLoading: false,
        loading:false,
        columns: [
          {
            dataField: "kode_gudang",
            text: "Gudang",
            sort: true,
            editable: false
          },
          {
            dataField: "kode_toko",
            text: "Baki",
            editable: false
          },
          {
            dataField: "qty",
            text: "Qty",
            editable: false
          },
          {
            dataField: "berat",
            text: "Berat",
            editable: false
          },
          {
            dataField: "berat_baki",
            text: "Berat baki",
            editable: false
          },
          {
            dataField: "berat_bandrol",
            text: "Berat Bandrol",
            editable: false,
            formatter : (data, row) => {
                  return parseFloat(row.berat_bandrol).toFixed(3)
            }
          },
          {
            dataField: "total",
            text: "Total",
            editable: false,
            formatter : (data, row) => {
                  return parseFloat(row.berat + row.berat_baki + row.berat_bandrol).toFixed(3)
            }
          },
          {
            dataField: "timbang",
            text: "Timbang",
          },
          {
            dataField: "selisih",
            text: "Selisih",
          },
        ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataGudang())
    this.props.dispatch(getDataBaki())
    // this.props.dispatch(getNokirim())
  }

  sendTimbangan(){
    let data = {
      tanggal : getToday(),
      detail_barang : this.props.timbang_barang
    }
    this.setState({
      isLoading: true
    })
    postData2("timbang-baki", data).then((res)=> {
      this.setState({
        isLoading: false
      })
      ToastNotification("success", 'Data Berhasil Disimpan')
      this.props.dispatch(updateTimbangBarang([]))
    }).catch((err)=> {
      this.setState({
        isLoading: false
      })
      console.log(err);
      ToastNotification("error", err && err.response && err.response.data && "Terjadi kersalahan saat menimbang baki")
    } )
  }

  render() {
    let databaki =  [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ];
    this.props.databaki.forEach(list => {
      let data = {
        value: list.kode_baki,
        name: list.kode_baki + " - " + list.nama_baki,
      };
      databaki.push(data)
    });
    return (
        <form onSubmit={this.props.handleSubmit} autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
          <div className="col-lg-4">
            <Field
              id="kode_gudang"
              label="Kode Gudang"
              name="kode_gudang"
              onChange={(e) => {
                this.setState({ kode_gudang: e });
                this.props.change("kode_baki", "");
                this.props.dispatch(getDataBakiByGudang(e))
              }}
              options={this.props.datagudang
                .filter((list) => list.kode_gudang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_gudang,
                    name: list.kode_gudang + " - " + list.nama_gudang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kode Gudang"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            <Field
              id="kode_baki"
              label="Kode Baki"
              name="kode_baki"
              onChange={(e) => {
                this.setState({ kode_baki: e });
                this.props.change("kode_baki", "");
              }}
              options={databaki}
              placeholder={"Silahkan Pilih Kode Baki"}
              component={ReanderSelect}
            />
          </div>
              <div className="col-lg-4">
                <label htmlFor="">&nbsp;</label>
                <button className="btn btn-outline-secondary btn-block">Cari</button>
              </div>
          </div>
          <BootstrapTable
              keyField="baki"
              data={ this.props.products || [] }
              columns={ this.state.columns || [] }
              cellEdit={ cellEditFactory({ mode: 'click',
              blurToSave: true,
              afterSaveCell: (oldValue, newValue, row, column) => {
                let data = {
                  kode_gudang : row.kode_gudang,
                  kode_toko : row.kode_toko,
                  qty : row.qty,
                  berat: row.berat,
                  berat_baki : row.berat_baki,
                  berat_bandrol: row.berat_bandrol,
                  total: parseFloat(row.berat_baki) + parseFloat(row.berat_bandrol) + parseFloat(row.berat),
                  timbang: parseFloat(newValue),
                  selisih : parseFloat((parseFloat(row.berat_baki + row.berat_bandrol + row.berat) - parseFloat(newValue || 0)).toFixed(3) || 0)
                }
                let cek =
                        localStoragedecryp("timbang_barang").length === 0
                          ? []
                          : JSON.parse(localStoragedecryp("timbang_barang"));
                let hasil_cek = cek.findIndex(
                  (hasil) => hasil.kode_toko === data.kode_toko
                );
                console.log(data);
                if (hasil_cek === -1) {
                  cek.push(data);
                  localStorageencryp("timbang_barang", JSON.stringify(cek));
                  this.props.dispatch(updateTimbangBarang(cek))
                } else {
                  cek[hasil_cek] = data
                  localStorageencryp("timbang_barang", JSON.stringify(cek));
                  this.props.dispatch(updateTimbangBarang(cek))
                }
              }}) }
            />
              <div className="row">
                <div className="col-lg-8"></div>
              <div className="col-lg-4 text-right">
                <label htmlFor="">&nbsp;</label>
                <button className="btn btn-primary btn-block" type="button" onClick={() => this.sendTimbangan()}>{this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}</button>
              </div>
              </div>
          {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
        </form>
    );
  }
}

FormTimbangBarang = reduxForm({
  form: "FormTimbangBarang",
  enableReinitialize: true,
})(FormTimbangBarang);
export default connect(maptostate,null)(FormTimbangBarang);
