import React from "react";
import { Link } from "react-router-dom";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { postData } from "../../../../config/axios.jsx";
import HeadFormBatalBeli from "./HeadFormBatalBeli.jsx";
import * as moment from "moment-timezone";

class FormLaporanBatalBeli extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      datapembelian: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading : true
    })
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };
    postData("batalpembelian/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
            datapembelian: [],
            export: false,
          })
        } else {
          ToastNotification("success", "Laporan Batal Beli Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading : false,
            datapembelian: res.data,
          });
        }
      })
      .catch((err) => {
        this.setState({
          isLoading : false,
        })
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Batal Pembelian</li>
        </ol>
        <h1 className="page-header">Laporan Batal Pembelian </h1>
        <Panel>
          <PanelHeader>Laporan Batal Pembelian</PanelHeader>
          <br />
          {/* Laporan Batal Pembelian */}
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFormBatalBeli
                  export={this.state.export}
                  isLoading={this.state.isLoading}
                  datapembelian={this.state.datapembelian}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
          <br />
          {/* End Tambah Laporan Batal Pembelian  */}
        </Panel>
      </div>
    );
  }
}

export default FormLaporanBatalBeli;
