import React from "react";
import { withRouter } from "react-router-dom";
import {
  getToday,
  localStoragedecryp,
} from "../../../components/helpers/function.jsx";
import { PageSettings } from "../../../config/page-settings.js";
class SettingNota extends React.Component {
  static contextType = PageSettings;

  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      // isSettingNota: false,
    };
  }

  state = {
    date: "",
  };

  getDate = () => {
    var date = new Date().toDateString();
    this.setState({ date });
  };

  componentDidMount() {
    const isAuthenticated = localStoragedecryp("islogin");
    if (isAuthenticated === getToday()) {
      this.props.history.push("/dashboard");
    }
    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener("popstate", function (event) {
      window.history.pushState(null, document.title, window.location.href);
    });

    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
    this.context.handleSetBodyWhiteBg(true);
    this.getDate();
    window.location.replace('http://localhost:3000/settingnota.html')
    // console.log(encryptascii('sa'));
    // console.log(decryptascii('BF95'));
  }

  componentWillUnmount() {
    this.context.handleSetPageSidebar(true);
    this.context.handleSetPageHeader(true);
    this.context.handleSetBodyWhiteBg(false);
  }


  render() {
    return (
     <> </>
    );
  }
}

export default withRouter(SettingNota);