import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import HeadTransaksiMember from "./HeadTransaksiMember.jsx";
import {
  formatDate,
  localStorageencryp,
} from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";
import { getData, postData } from "../../../config/axios.jsx";
import { reduxForm, reset } from "redux-form";
import { connect } from "react-redux";
import Loading from 'react-fullscreen-loading';

class LihatTransaksiMember extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      point: "POINT",
      datatransaksimember: [],
      columns: [
        {
          dataField: "tgl_trx",
          text: "Tanggal Trx",
          sort: true,
        },
        {
          dataField: "kategori",
          text: "Kategori",
        },
        {
          dataField: "deskripsi",
          text: "Deskripsi",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toFixed(3)}</span>;
          },
        },
        {
          dataField: "jumlah_rp",
          text: "Jumlah Rp",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toLocaleString("kr-KO")}</span>;
          },
        },
        {
          dataField: "poin_awal",
          text: "Poin Awal",
          formatter: (value, row) => {
            return value || 0;
          },
        },
        {
          dataField: "poin",
          text: "Poin",
          formatter: (value, row) => {
            return value || 0;
          },
        },
        {
          dataField: "poin_akhir",
          text: "Poin Akhir",
          formatter: (value, row) => {
            return value || 0;
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getKodeMember = this.getKodeMember.bind(this);
  }
  batalLihatTransaksi() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      this.setState({
        isLoadingBatal: false,
        datatransaksimember: []
      });
      this.props.dispatch(reset("HeadTransaksiMember"));
    }, 1000);
  }
  getKodeMember(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      if(e.kode_member === undefined){
        return false;
      }
      this.setState({
        isLoading: true
      })
      getData("member/get/by-kode-member/" + e.kode_member).then((res) => {
        // console.log(res.data)
        this.setState({
          isLoading: false
        });
        res.data.forEach((item, index) => {
          document.getElementById('lihat_transaksi_nama_member').value = item.nama_customer
          document.getElementById('lihat_transaksi_alamat').value = item.alamat_customer
          document.getElementById('lihat_transaksi_no_hp').value = item.no_hp
          // this.props.change("point", item.point);
          this.setState({
            datatransaksimember: [],
            point: item.point || 0,
            isLoading: false
          });
        });
        // console.log(res.data);
      }).catch((err)=> {
        ToastNotification("info", "Data member tidak ada")
        this.setState({
          datatransaksimember: [],
          isLoading: false
        });
      })
    }, 1500);
  }
  handleSubmit(data) {
    //   console.log(data);
    let data_post = {
      kode_member: data.kode_member,
      tgl_awal: formatDate(
        moment(new Date(data.dari_tanggal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.sampai_tanggal)).format("YYYY-MM-DD")
      ),
    };

    postData("trxmember/get/by-tanggal", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Member Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            datatransaksimember: res.data,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          isLoading: false,
          export: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Member</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Transaksi Member</li>
        </ol>
        <h1 className="page-header">Lihat Transaksi Member </h1>
        <Panel>
          <PanelHeader>Lihat Transaksi Member</PanelHeader>
          <br />
          {/* Lihat Transaksi Member */}
          <div className="container">
            <HeadTransaksiMember
              onChange={(e)=>this.getKodeMember(e)}
              batalLihatTransaksi={() => this.batalLihatTransaksi()}
              onSubmit={(data) => this.handleSubmit(data)}
              columns={this.state.columns}
              point={this.state.point}
              isLoadingBatal={this.state.isLoadingBatal}
              datatransaksimember={this.state.datatransaksimember}
            />
            <br />
          </div>
          <br />
          {/* End Tambah Lihat Transaksi Member  */}
        </Panel>
        {this.state.isLoadingBatal || this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

LihatTransaksiMember = reduxForm({
  form: "LihatTransaksiMember",
  enableReinitialize: true,
  // validate: ValidasiDataUsers,
})(LihatTransaksiMember);
export default connect( null)(LihatTransaksiMember);