import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import { showModalLihatHutang } from "../../../actions/hutang_action.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { postData } from "../../../config/axios.jsx";
import ModalPembayaran from "./ModalPembayaran.jsx";
import HeadLihatHutang from "./HeadLihatHutang.jsx";
import PelunasanHutang from "./PelunasanHutang.jsx";
import { formatDate, getUserdata } from "../../../components/helpers/function.jsx";
import BatalHutang from "./BatalHutang.jsx";
import * as moment from "moment-timezone";
import Swal from "sweetalert2";


class FormLihatHutang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      datahutang: [],
      isLoadingPembayaran: false,
      isloadingBtl: false,
      formPelunasan: "LIHATHUTANG",
      columns: [
        {
          dataField: "no_faktur_hutang",
          text: "No Faktur",
          footerAttrs: {
            colSpan: "3",
          },
          footer: "",
        },
        {
          dataField: "tgl_hutang",
          text: "Tanggal Hutang",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        {
          dataField: "jumlah_hutang",
          text: "Jumlah Hutang",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            //  
            // console.log(row.tgl_system);
            return (
              <div className="row text-center">
                <div className="col-12">
                  {getUserdata().tgl_system === row.tgl_system ? (
                    <>
                      <button
                        type="button"
                        data-tooltip="Batal Hutang"
                        onClick={() => this.getDetailHutang(row)}
                        className="btn btn-danger mr-3"
                      >
                        <i
                          data-tip="Batal Hutang"
                          className="fa fa-window-close"
                        ></i>
                      </button>
                    </>
                  ) : null}
                  <button
                    type="button"
                    data-tooltip="Pelunasan Hutang"
                    onClick={() => this.showModalLihatHutang(row)}
                    className="btn btn-success mr-3"
                  >
                    <i
                      data-tip="Pelunasan Hutang"
                      className="fa fa-calendar-check"
                    ></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
  }

  showModalLihatHutang(data) {
    this.setState({
      formPelunasan: "PELUNASANHUTANG",
    });
    this.props.dispatch(showModalLihatHutang(data));
  }
  getDetailHutang(row) {
    this.setState({
      formPelunasan: "BATALHUTANG",
    });
    this.props.dispatch(showModalLihatHutang(row));
  }
  cariHutang(data) {
    //   console.log(data);
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    postData("hutang/get/by-tanggal", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            isLoading: false,
            datahutang: [],
          });
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          return false;
        } else {
          ToastNotification("success", "Data Hutang Tersedia");
          this.setState({
            datahutang: res.data,
            isLoading: false,
          });
        }
      })
      .catch((err) => {
        this.setState({
          isLoading: false,
          datahutang: [],
        });
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }
  back() {
    this.setState({ formPelunasan: "LIHATHUTANG" });
  }
  showModalPembayaran() {
    this.props.dispatch(showModal());
  }

  simpanPembayaran(data) {
    let no_faktur = document.getElementById("no_faktur_hutang").value;
    let data_pembayaran = {
      jenis: data.jenis,
      jumlah_rp: parseInt(data.cash),
    };
    if (parseInt(data.cash) !== parseInt(data.grand_total_pembayaran)) {
      Swal.fire({
        position: "center",
        icon: "warning",
        title:
          `Anda yakin ingin membayar Rp.${parseInt(data.cash).toLocaleString("kr-KO")}`,
        showConfirmButton: true,
        showCancelButton: true,
        confirmButtonText: "YAKIN",
        confirmButtonColor: "success"
      }).then((result) => {
        if (result.isConfirmed) {
          let hasil = {
            no_faktur_hutang: no_faktur,
            tgl_lunas: document.getElementById('tgl_lunas_hutang').value,
            jumlah_bayar: parseInt(data.cash),
            pembayaran: [data_pembayaran],
          };
          this.setState({
            isLoadingPembayaran: true,
          });
          postData("hutang/hutanglunas", hasil)
            .then((res) => {
              this.setState({
                isLoadingPembayaran: false,
                formPelunasan: "LIHATHUTANG",
                datahutang: [],
              });
              this.props.dispatch(hideModal());
              localStorage.removeItem("jml_byar_pelunasan_hutang");
              ToastNotification("success", "Hutang Berhasil Di Lunasi");
            })
            .catch((err) => {
              this.setState({
                isLoadingPembayaran: false,
              });
              ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
            });
        } else {
          return false;
        }
      });
    } else {
        let hasil = {
          no_faktur_hutang: no_faktur,
          tgl_lunas: document.getElementById('tgl_lunas_hutang').value,
          jumlah_bayar: parseInt(data.cash),
          pembayaran: [data_pembayaran],
        };
        this.setState({
          isLoadingPembayaran: true,
        });
        postData("hutang/hutanglunas", hasil)
          .then((res) => {
            this.setState({
              isLoadingPembayaran: false,
              formPelunasan: "LIHATHUTANG",
              datahutang: [],
            });
            this.props.dispatch(hideModal());
            localStorage.removeItem("jml_byar_pelunasan_hutang");
            ToastNotification("success", "Hutang Berhasil Di Lunasi");
          })
          .catch((err) => {
            this.setState({
              isLoadingPembayaran: false,
            });
            ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
          });
      }
}
  btlHutang(data) {
    this.setState({
      isloadingBtl: true,
    });
    let hasil = {
      no_faktur_hutang: data.no_faktur_hutang,
    };
    postData("hutang/hutangbatal", hasil)
      .then((res) => {
        ToastNotification("success", "Hutang Berhasil Di Batal");
        this.setState({
          isloadingBtl: false,
          formPelunasan: "LIHATHUTANG",
          datahutang: [],
        });
        this.props.dispatch(hideModal());
      })
      .catch((err) => {
        this.setState({
          isloadingBtl: false,
        });
        ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data");
      });
  }
  render() {
    // const berat = 0;
    // const jml = 0;
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              <table>
                <tr>
                  <td>Nama Barang</td>
                  <td>Berat</td>
                  <td>Jumlah</td>
                </tr>
                {row.detail_barang.map((item, index) => (
                  <tr>
                    <td>{item.nama_barang}</td>
                    <td>{item.berat}</td>
                    <td>{item.jumlah}</td>
                  </tr>
                ))}
              </table>
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Hutang</Link>
          </li>
          <li className="breadcrumb-item active"> {this.state.formPelunasan === "PELUNASANHUTANG"
              ? "Pelunasan Hutang"
              : this.state.formPelunasan === "BATALHUTANG"
              ? "BATAL HUTANG"
              : "Lihat Hutang"}</li>
        </ol>
        <h1 className="page-header"> {this.state.formPelunasan === "PELUNASANHUTANG"
              ? "Pelunasan Hutang"
              : this.state.formPelunasan === "BATALHUTANG"
              ? "BATAL HUTANG"
              : "Lihat Hutang"} </h1>
        <Panel>
          <PanelHeader>
            {this.state.formPelunasan === "PELUNASANHUTANG"
              ? "Pelunasan Hutang"
              : this.state.formPelunasan === "BATALHUTANG"
              ? "BATAL HUTANG"
              : "Lihat Hutang"}
          </PanelHeader>
          <br />
          {/* Lihat Hutang */}
          <div className="container">
            {this.state.formPelunasan === "PELUNASANHUTANG" ? (
              <>
                {" "}
                <PelunasanHutang
                  showModalPembayaran={() => this.showModalPembayaran()}
                  back={() => this.back()}
                />{" "}
              </>
            ) : this.state.formPelunasan === "BATALHUTANG" ? (
              <BatalHutang
                isloadingBtl={this.state.isloadingBtl}
                onSubmit={(data) => this.btlHutang(data)}
                back={() => this.back()}
              />
            ) : (
              <>
                <HeadLihatHutang
                  onSubmit={(data) => this.cariHutang(data)}
                  isLoading={this.state.isLoading}
                />
                <div className="row">
                  <div className="col-12">
                    <Tabel
                      keyField="no_faktur_hutang"
                      exportCsv={false}
                      data={this.state.datahutang}
                      columns={this.state.columns}
                      empty={true}
                      expandRow={expandRow}
                      textEmpty="Data Barang Kosong"
                    />
                  </div>
                </div>
              </>
            )}
          </div>
          <br />
          {/* End Tambah Lihat Hutang  */}
          {/* <ModalGlobal
            // size="L"
            title="Pelunasan Hutang"
            content={<PelunasanHutang back={()=>this.back()} />}
          /> */}
          <ModalGlobal
            title="Form Pembayaran"
            content={
              <ModalPembayaran
                isLoadingPembayaran={this.state.isLoadingPembayaran}
                onSubmit={(data) => this.simpanPembayaran(data)}
              />
            }
          />
        </Panel>
      </div>
    );
  }
}

export default connect(null)(FormLihatHutang);
